<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('../../assets/logo-white.png')"
              contain
              height="120"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">Documentos en línea</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12">Categoria > Subcategoría</v-col>
      </v-row>

      <v-row class="text-center">
        <v-col v-for="(item, i) in items" :key="i" cols="4">
          <template v-if="item.enabled">
            <div @click="goTo(item)">
              <div style="background-color: #EDEDED; border-radius: 1rem; padding: 0.5rem">
                <v-img
                    :src="require('../../assets/icons/home-07.svg')"
                    class="my-3"
                    contain
                    height="48"
                />
              </div>
              <div class="mt-2">{{ item.name }}</div>
            </div>
          </template>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import {mapActions, mapGetters} from "vuex"
import axios from "axios"

export default {
  name: 'DocumentsLevel2',
  data: () => ({
    category: '',
    subcategory: '',
    items: []
  }),
  computed: {
    ...mapGetters(['user']),
  },
  mounted() {
    this.loadDocuments()
  },
  methods: {
    ...mapActions(['selectOperation']),
    goTo(item) {
      this.selectOperation(item)

      if (item.variable_price) {
        this.$router.push({name: 'DocumentVariableIndex', params: {id: item.id}})
        return
      }

      this.$router.push({
        name: 'DocumentsDetail', params: {
          id: this.category.id,
          id2: this.subcategory.id,
          id3: item.id,
        }
      })
    },
    loadDocuments() {
      let url = 'categories?type=document'
      axios.get(url).then((response) => {
        let id = this.$route.params.id
        let id2 = this.$route.params.id2
        this.category = response.data.find(obj => parseInt(obj.id) === parseInt(id))
        this.subcategory = this.category.subcategories.find(obj => parseInt(obj.id) === parseInt(id2))
        this.items = this.subcategory.links
      })
    }
  }
}
</script>
